<template>
  <header class="position-relative bg-white">
    <section class="mt-2 d-none d-sm-flex flex-column mb-3 container">
      <div class="ml-auto">
        <input class="px-2" type="text" name id placeholder="SEARCH">
      </div>
      <div class="row d-flex">
        <router-link class="col-2 text-primary h1 mb-0" to="/">
          Lativ
        </router-link>
        <div class="col-10 d-flex align-items-end">
          <ul class="d-inline-flex align-items-center mb-0">
            <li class="px-2" @click="selectedNav = 'women'" >
              <router-link class="nav_link px-2"
              :to="{ name: 'cardListIndex', params: { gender: 'women'}}"
              :class="{'nav_link_select' : selectedNav === 'women'}">
                WOMEN
              </router-link>
            </li>
            <div class="py-1 border-right"></div>
            <li class="px-2" @click="selectedNav = 'men'">
              <router-link class="nav_link px-2" 
              :class="{'nav_link_select' : selectedNav === 'men','nav_link': selectedNav !== 'men'}"
              :to="{ name: 'cardListIndex', params: { gender: 'men' }}">
                MEN
              </router-link>
            </li>
          </ul>
          <ul class="d-inline-flex align-items-end mb-0 ml-auto">
            <li class="ml-auto d-flex text-primary">
              <a href class="line-r mx-2 h6">
                訂閱電子報
              </a>
              <a href class="mx-2 h6">
                登入/註冊
              </a>
              <a href class="ml-2 position-relative h6" @mouseenter="isHoverCart = true" @mouseleave="isHoverCart = false">
                {{allQty}}個商品
                <div v-if="isAddToCart" class="add-cart-show position-absolute border">
                  <h6 class="text-center text-primary my-3">本商品已經加入購物車</h6>
                  <div class="border-top border-bottom w-75 mb-3 py-3 mx-auto">
                    <img :src="lastCartProduct.imgUrl" class="d-inline-block w-25" alt>
                    <h6 class="d-inline-block w-75 pl-1">{{showCartProduct(cart.length-1)}}</h6>
                  </div>
                  <div class="d-flex mb-3 mr-3">
                    <a href class="check-btn ml-auto bg-primary text-white px-3 py-1 rounded">前往結帳</a>
                  </div>
                </div>
                <div v-if="isHoverCart" class="add-cart-show position-absolute border">
                  <table class="text-center w-75 mb-3 py-3 mx-auto">
                    <tr class="border-bottom">
                      <th>商品名稱</th>
                      <th>顏色</th>
                      <th>尺寸</th>
                      <th>數量</th>
                    </tr>
                    <tr v-for="(item,index) in cart" :key="index">
                      <td>{{item.title}}</td>
                      <td>{{item.colorName}}</td>
                      <td>{{item.sizeName}}</td>
                      <td>{{item.qty}}</td>
                    </tr>
                  </table>
                  <div class="d-flex mb-3 mr-3">
                    <a href class="check-btn ml-auto bg-primary text-white px-3 py-1 rounded">前往結帳</a>
                  </div>
                </div>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </section>
    <section class="mobile-header d-block d-sm-none position-fixed bg-white w-100 p-2">
      <div class="d-flex w-100">
        <div class="text-white bg-primary px-1 py-1 flex-center">Lativ</div>
        <div class="rounded d-flex mx-3 flex-grow-1">
          <i class="nav-search px-3 fas fa-search flex-center"></i>
          <input class="nav-search flex-grow-1 mr-2 border-0" type="text" name id placeholder="輸入關鍵字">
        </div>
        <div class="px-1 py-1 position-relative h6">
          <i class="far fa-comment"></i>
            消息
        </div>
      </div>
      <ul class="d-flex text-unclick mb-0 mt-2">
        <li class="px-2">首頁</li>
        <li class="px-2">限時特惠</li>
        <li class="px-2">女裝</li>
        <li class="px-2">男裝</li>
        <li class="px-2">童裝</li>
      </ul>
    </section>
  </header>
</template>
<script>
export default {
  data() {
    return {
      selectedNav: "",
      isHoverCart: false
    };
  },
  computed: {
    allQty() {
      return this.$store.state.cart.length;
    },
    isAddToCart() {
      return this.$store.state.isAddToCart;
    },
    lastCartProduct() {
      return this.$store.state.cart[this.$store.state.cart.length - 1];
    },
    cart() {
      return this.$store.state.cart;
    }
  },
  methods: {
    showCartProduct(index) {
      const string = `${this.cart[index].title}-${this.cart[index].gender} ${
        this.cart[index].colorName
      }-${this.cart[index].title.sizeName} NT$${this.cart[index].qty *
        this.cart[index].price}`;
      return string;
    }
  }
};

</script>
<style lang="scss" scoped>
.add-cart-show {
  right: 0;
  width: 400px;
  z-index: 20;
  background-color: #fff;
}

tr,
th {
  font-size: 12px;
  padding-top: 6px;
  padding-bottom: 6px;
}

.check-btn {
  font-size: 12px;
}

.mobile-header {
  top: 0;
  z-index: 100;
  right: 0;
}

.nav_link{
  font-size: 16px;
  color: #858585;
  text-decoration: none;
  &:hover{
    color: #4d3126;
  }
}

.fa-comment {
  height: 12px;
}

.nav-search {
  background-color: #eeeeee;
}
.fa-search {
  color: #c6c6c6;
  font-size: 20px;
}
.nav_link_select{
  color: #5d3b24;
  background-color: #f0ede5;
}
header{
  height:105px;
  @media (min-width: 410px) and (max-width: 575px) {
    height: 80px;
  }
  @media (min-width: 575.98px) {
    height: auto;
  }

}
</style>
